import {
	EXCHANGECARD,
	MYINFO
} from '@/utils/api.js'
export default {
	data() {
		return {
			isData: false,
			myInfo: '',
			myCamilo: '',
			isdisable: false,
			type: ''
		}
	},
	onLoad(options) {
		this.type = options.type
		// 我的信息
		this.getInfo()
	},
	methods: {
		getInfo() {
			const params = {}
			this.$request.url_request(MYINFO, params, 'GET', res => {
				this.myInfo = JSON.parse(res.data).data
			}, err => {})
		},
		// 兑换
		exchange() {
			this.isdisable = true
			var _this = this
			setTimeout(function() {
				_this.isdisable = false
				_this.$forceUpdate()
			}, 1000)
			if (this.myCamilo == '') {
				uni.showToast({
					title: '卡密不能为空',
					icon: 'success',
					duration: 1000
				})
			} else {
				var _this = this
				const params = {
					card: this.myCamilo
				}
				_this.$request.url_request(EXCHANGECARD, params, 'GET', res => {
					if (JSON.parse(res.data).code == 0) {
						uni.showToast({
							title: '兑换成功',
							icon: 'success',
							duration: 1000
						})
						if (_this.type == 1) {
							setTimeout(() => {
								uni.reLaunch({
									url: '../Welcome/guideNew/guideNew'
								})
							},1000)
						} else {
							setTimeout(() => {
								_this.getInfo()
							}, 1000)
						}

					} else {
						if (JSON.parse(res.data).msg) {
							uni.showToast({
								title: JSON.parse(res.data).msg,
								icon: 'none',
								duration: 1200
							})
						}
					}
				}, err => {})
			}
		}
	}
}
